import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

/**
 * Generated class for the MoreInfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-more-info',
  templateUrl: 'more-info.html',
})
export class MoreInfoPage {
  public exterioresKey : any ;
  public interioresKey: any;
  public servicesKey: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
    //console.log(navParams);
  }

  ionViewDidLoad() {
    //console.log(this.navParams.data);


    if (this.navParams.data.exterior != 0 && this.navParams.data.exterior !== undefined){
      this.exterioresKey = Object.keys(this.navParams.data.exterior);
    }


    if (this.navParams.data.interiores != 0 && this.navParams.data.interiores !== undefined) {
      this.interioresKey = Object.keys(this.navParams.data.interiores);
    }


    if (this.navParams.data.services != 0 && this.navParams.data.services !== undefined) {
      this.servicesKey = Object.keys(this.navParams.data.services);
    }



    //console.log('ionViewDidLoad MoreInfoPage');
  }

  close(){
    this.viewCtrl.dismiss();

  }
}



